import { Injectable } from '@angular/core';
import { AsyncSubject } from 'rxjs';
import { AS_COMPLETE } from './ngx-scanner-qrcode.helper';
import { ScannerQRCodeResult } from './ngx-scanner-qrcode.options';


@Injectable({
  providedIn: 'root'
})
export class NgxScannerQrcodeService {

  /**
   * Load files
   * @param files 
   * @return AsyncSubject
   */
  public loadFiles(files: File[] = []): AsyncSubject<ScannerQRCodeResult[]> {
    const as = new AsyncSubject<ScannerQRCodeResult[]>();
    Promise.all(Object.assign([], files).map(m => this.readAsDataURL(m))).then((img: ScannerQRCodeResult[]) => AS_COMPLETE(as, img)).catch((error: any) => AS_COMPLETE(as, null, error));
    return as;
  }

  /**
   * readAsDataURL
   * @param file 
   * @return Promise
   */
  private readAsDataURL(file: File): Promise<ScannerQRCodeResult> {
    /** drawImage **/
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.onload = () => {
        const objectFile = {
          name: file.name,
          file: file,
          base64: fileReader.result as string,
          url: URL.createObjectURL(file)
        };
        resolve(objectFile);
      }
      fileReader.onerror = (error: any) => reject(error);
      fileReader.readAsDataURL(file);
    })
  }
}