import { Component, ElementRef, EventEmitter, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, AsyncSubject } from 'rxjs';
import jsQR from 'jsqr';
import { AS_COMPLETE, DRAW_RESULT, HAS_OWN_PROPERTY, OVERRIDES, PLAY_AUDIO } from './ngx-scanner-qrcode.helper';
import { BaseConfig, ScannerQRCodeResult } from './ngx-scanner-qrcode.options';
import { CONFIG_DEFAULT, MEDIA_STREAM_DEFAULT } from './ngx-scanner-qrcode.default';


@Component({
  selector: 'ngx-scanner-qrcode',
  templateUrl: './ngx-scanner-qrcode.component.html',
  styleUrls: ['./ngx-scanner-qrcode.component.scss'],
  host: { 'class': 'ngx-scanner-qrcode' },
  exportAs: 'scanner',
  inputs: ['src', 'isAuto', 'isBeep', 'isAlwaysEmit', 'config', 'medias'],
  outputs: ['event', 'error'],
  queries: {
    video: new ViewChild('video'),
    canvas: new ViewChild('canvas'),
    resultsPanel: new ViewChild('resultsPanel')
  },
  encapsulation: ViewEncapsulation.None
})
export class NgxScannerQrcodeComponent implements OnInit, OnDestroy {

  /**
   * Element
   * playsinline required to tell iOS safari we don't want fullscreen
   */
  public video!: ElementRef<HTMLVideoElement>;
  public canvas!: ElementRef<HTMLCanvasElement>;
  public resultsPanel!: ElementRef<HTMLDivElement>;

  /**
   * EventEmitter
   */
  public error = new EventEmitter<any | null>();
  public event = new EventEmitter<ScannerQRCodeResult>();

  /**
   * Input
   */
  public src: string | undefined = CONFIG_DEFAULT.src;
  public isAuto: boolean | undefined = CONFIG_DEFAULT.isAuto;
  public isBeep: boolean | undefined = CONFIG_DEFAULT.isBeep;
  public isAlwaysEmit: boolean | undefined = CONFIG_DEFAULT.isAlwaysEmit;
  public config: BaseConfig = CONFIG_DEFAULT;
  public medias: MediaStreamConstraints | undefined = CONFIG_DEFAULT.medias;

  /**
   * Export
   */
  public isStart: boolean = false;
  public isLoading: boolean = false;
  public data = new BehaviorSubject<ScannerQRCodeResult>({});

  /**
   * Private
   */
  private rAF_ID: any;

  ngOnInit(): void {
    this.overrideConfig();
    if (this.src) {
      this.loadImage(this.src);
    } else if (this.isAuto) {
      this.start();
    }
    this.resize();
  }

  /**
   * start
   * @return AsyncSubject
   */
  public start(): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isStart) {
      // Reject
      AS_COMPLETE(as, false);
    } else {
      // Loading on
      this.status(false, true);
      /**
       * MediaStream
       * Use facingMode: environment to attemt to get the front camera on phones
       */
      navigator.mediaDevices.getUserMedia(this.medias).then((stream: MediaStream) => {
        this.video.nativeElement.srcObject = stream;
        this.video.nativeElement.onloadedmetadata = () => {
          this.video.nativeElement.play();
          this.requestAnimationFrame();
          this.status(true, false);
          AS_COMPLETE(as, true);
        }
      }).catch(error => {
        this.status(false, false);
        this.eventEmit(false, error);
        AS_COMPLETE(as, false, error);
      });
    }

    return as;
  }

  /**
   * stop
   * @return AsyncSubject
   */
  public stop(): AsyncSubject<any> {
    this.eventEmit(null);
    this.status(false, false);
    const as = new AsyncSubject<any>();
    try {
      this.removeCanvas();
      clearInterval(this.rAF_ID);
      (this.video.nativeElement.srcObject as MediaStream).getTracks().forEach((track: any) => {
        track.stop();
        AS_COMPLETE(as, true);
      });
    } catch (error) {
      this.eventEmit(false, error);
      AS_COMPLETE(as, false, error as any);
    }
    return as;
  }

  /**
   * play
   * @return AsyncSubject
   */
  public play(): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isPause) {
      this.video.nativeElement.play();
      this.requestAnimationFrame();
      AS_COMPLETE(as, true);
    } else {
      AS_COMPLETE(as, false);
    }
    return as;
  }

  /**
   * pause
   * @return AsyncSubject 
   */
  public pause(): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    if (this.isStart) {
      clearInterval(this.rAF_ID);
      this.video.nativeElement.pause();
      AS_COMPLETE(as, true);
    } else {
      AS_COMPLETE(as, false);
    }
    return as;
  }

  /**
   * loadImage
   * @param src 
   */
  public loadImage(src: string): AsyncSubject<any> {
    const as = new AsyncSubject<any>();
    // Loading on
    this.status(false, true);
    // Set the src of this Image object.
    const image = new Image();
    // Setting cross origin value to anonymous
    image.setAttribute('crossOrigin', 'anonymous');
    // When our image has loaded.
    image.onload = () => {
      this.drawImage(image, (flag: boolean) => { 
        this.status(false, false);
        AS_COMPLETE(as, flag);
      });
    };
    // Set src
    image.src = src;
    return as;
  }

  /**
   * window: resize
   * Draw again!
   */
  private resize(): void {
    window.addEventListener("resize", () => this.data.subscribe(res => {
      if (HAS_OWN_PROPERTY(res, 'location')) {
        DRAW_RESULT(res as any, this.canvas.nativeElement, this.resultsPanel.nativeElement);
      } 
    }));
  }

  /**
   * Override config
   * @return void
   */
  private overrideConfig(): void {
    if (HAS_OWN_PROPERTY(this.config, 'src')) this.src = this.config.src;
    if (HAS_OWN_PROPERTY(this.config, 'isAuto')) this.isAuto = this.config.isAuto;
    if (HAS_OWN_PROPERTY(this.config, 'isBeep')) this.isBeep = this.config.isBeep;
    if (HAS_OWN_PROPERTY(this.config, 'isAlwaysEmit')) this.isAlwaysEmit = this.config.isAlwaysEmit;
    if (HAS_OWN_PROPERTY(this.config, 'medias')) this.medias = OVERRIDES('medias', this.config, MEDIA_STREAM_DEFAULT);
  }

  /**
   * drawImage
   * @param element 
   * @param callback 
   */
  private drawImage(element: HTMLImageElement | HTMLVideoElement, callback: Function = () => {}): void {
    // Get the canvas element by using the getElementById method.
    const canvas = this.canvas.nativeElement;
    // Get a 2D drawing context for the canvas.
    const ctx = canvas.getContext('2d', { willReadFrequently: true }) as CanvasRenderingContext2D;
    // HTMLImageElement size
    if (element instanceof HTMLImageElement) {
      canvas.width = element.naturalWidth;
      canvas.height = element.naturalHeight;
      element.style.visibility = '';
      this.video.nativeElement.style.visibility = 'hidden';
    }
    // HTMLVideoElement size
    if (element instanceof HTMLVideoElement) {
      canvas.width = element.videoWidth;
      canvas.height = element.videoHeight;
      element.style.visibility = '';
      this.canvas.nativeElement.style.visibility = 'hidden';
    }
    // Draw image
    ctx.drawImage(element, 0, 0, canvas.width, canvas.height);
    // Data image
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    // Draw frame
    const code = jsQR(imageData.data, imageData.width, imageData.height, { inversionAttempts: "attemptBoth" }); /* "dontInvert" | "onlyInvert" | "attemptBoth" | "invertFirst" */
    if (code) {
      DRAW_RESULT(code, Object.freeze(this.canvas.nativeElement), this.resultsPanel.nativeElement);

      // toBlob and emit data
      const EMIT_DATA = () => this.eventEmit({...code, canvas: canvas});
     
      // HTMLImageElement
      if (element instanceof HTMLImageElement) {
        callback(true);
        EMIT_DATA();
        PLAY_AUDIO(this.isBeep);
      } 
      // HTMLVideoElement
      if (element instanceof HTMLVideoElement) {
        if (this.isAlwaysEmit) {
          EMIT_DATA();
          PLAY_AUDIO(this.isBeep);
        } else if (code.data !== (this.data.value as any)['data']) {
          EMIT_DATA();
          PLAY_AUDIO(this.isBeep);
        }
      }
    } else {
      callback(false);
      this.removeCanvas();
    }
  }

  /**
   * removeCanvas
   */
  private removeCanvas(): void {
    Object.assign([], this.resultsPanel.nativeElement.childNodes).forEach(el => this.resultsPanel.nativeElement.removeChild(el));
  }

  /**
   * status
   * @param isStart 
   * @param isLoading 
   */
  private status(isStart: boolean, isLoading: boolean): void {
    this.isStart = isStart;
    this.isLoading = isLoading;
  }

  /**
   * eventEmit
   * @param response 
   * @param error 
   */
  private eventEmit(response: any = false, error: any = false): void {
    (response !== false) && this.data.next(response || { data: null });
    (response !== false) && this.event.emit(response || { data: null });
    (error !== false) && this.error.emit(error || null);
  }

  /**
   * Single-thread
   * Loop Recording on Camera
   * Must be destroy request 
   * requestAnimationFrame
   */
  private requestAnimationFrame(): void {
    this.rAF_ID = setInterval(() => {
      if (this.video.nativeElement.readyState === this.video.nativeElement.HAVE_ENOUGH_DATA) {
        this.drawImage(this.video.nativeElement);
      }
    }, 40);
  }

  /**
   * Status of camera
   * @return boolean
   */
  get isPause(): boolean {
    return this.isStart && this.video.nativeElement.paused;
  }

  ngOnDestroy(): void {
    this.pause();
  }
}